// app/documents/page.tsx
"use client";

import React, { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import {
  ArrowPathIcon,
  DocumentTextIcon,
  MagnifyingGlassIcon,
  FunnelIcon,
  DocumentArrowDownIcon,
  ClockIcon,
  ExclamationTriangleIcon,
} from "@heroicons/react/24/outline";

type ComparisonItem = {
  id: number;
  document_name: string;
  version_old_label: string;
  version_new_label: string;
  created_at: string;
  overall_risk_level?: "LOW" | "MEDIUM" | "HIGH" | string | null;
  changes_count?: number;
};

type Risk = "HIGH" | "MEDIUM" | "LOW";

const API_BASE =
  process.env.NEXT_PUBLIC_API_BASE?.trim() || "http://127.0.0.1:8000";

/* ---------------- utils ---------------- */

function normalizeRisk(r?: string | null): Risk {
  const x = (r || "").toUpperCase();
  if (x.includes("HIGH")) return "HIGH";
  if (x.includes("MEDIUM")) return "MEDIUM";
  return "LOW";
}

function formatDate(iso: string) {
  try {
    return new Date(iso).toLocaleString("th-TH", {
      month: "short",
      day: "numeric",
      year: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  } catch {
    return iso;
  }
}

function csvEscape(v: any) {
  const s = String(v ?? "");
  if (/[",\n]/.test(s)) return `"${s.replaceAll('"', '""')}"`;
  return s;
}

function RiskPill({ r }: { r: Risk }) {
  const base =
    "inline-flex items-center rounded-full px-2.5 py-1 text-[11px] font-bold border";
  if (r === "HIGH")
    return <span className={`${base} bg-red-50 text-red-600 border-red-200`}>HIGH</span>;
  if (r === "MEDIUM")
    return <span className={`${base} bg-yellow-50 text-yellow-700 border-yellow-200`}>MEDIUM</span>;
  return <span className={`${base} bg-emerald-50 text-emerald-600 border-emerald-200`}>LOW</span>;
}

function clamp(n: number, min: number, max: number) {
  return Math.max(min, Math.min(max, n));
}

/* ---------------- page ---------------- */

export default function DocumentsPage() {
  const [items, setItems] = useState<ComparisonItem[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const [q, setQ] = useState("");
  const [riskFilter, setRiskFilter] = useState<"ALL" | Risk>("ALL");
  const [sort, setSort] = useState<"UPDATED_DESC" | "NAME_ASC" | "RISK_DESC">(
    "UPDATED_DESC"
  );

  const fetchComparisons = async () => {
    setLoading(true);
    setError(null);
    try {
      const res = await fetch(`${API_BASE}/comparisons?limit=250`, {
        cache: "no-store",
      });
      if (!res.ok) throw new Error(`โหลดข้อมูลไม่สำเร็จ (${res.status})`);
      const data: ComparisonItem[] = await res.json();
      setItems(Array.isArray(data) ? data : []);
    } catch (e: any) {
      setError(e?.message || "เกิดข้อผิดพลาด");
      setItems([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchComparisons();
  }, []);

  const computed = useMemo(() => {
    const list = [...items].sort(
      (a, b) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime()
    );

    const byDoc = new Map<string, any>();

    for (const x of list) {
      const doc = (x.document_name || "Untitled").trim();
      const cur =
        byDoc.get(doc) || {
          doc,
          comparisons: 0,
          vset: new Set<string>(),
          totalChanges: 0,
          lastUpdatedISO: x.created_at,
          lastComparisonId: x.id,
          lastRisk: normalizeRisk(x.overall_risk_level),
          lastFromTo: `${x.version_old_label} → ${x.version_new_label}`,
        };

      cur.comparisons += 1;
      if (x.version_old_label) cur.vset.add(x.version_old_label);
      if (x.version_new_label) cur.vset.add(x.version_new_label);
      cur.totalChanges += x.changes_count ?? 0;

      if (!byDoc.has(doc)) byDoc.set(doc, cur);
    }

    const rows = [...byDoc.values()].map((x) => ({
      ...x,
      versionsEstimated: x.vset.size,
    }));

    const qq = q.toLowerCase();
    let filtered = rows.filter(
      (r) =>
        (!qq || r.doc.toLowerCase().includes(qq)) &&
        (riskFilter === "ALL" || r.lastRisk === riskFilter)
    );

    filtered.sort((a, b) => {
      if (sort === "NAME_ASC") return a.doc.localeCompare(b.doc);
      return new Date(b.lastUpdatedISO).getTime() - new Date(a.lastUpdatedISO).getTime();
    });

    const topChanged = [...rows]
      .sort((a, b) => b.totalChanges - a.totalChanges)
      .slice(0, 5);

    return {
      filtered,
      totalDocs: rows.length,
      totalComparisons: list.length,
      topChanged,
      maxChanges: Math.max(1, ...topChanged.map((x) => x.totalChanges)),
    };
  }, [items, q, riskFilter, sort]);

  /* ---------------- UI ---------------- */

  return (
    <div className="max-w-6xl mx-auto space-y-5">
      {/* Header */}
      <div className="rounded-lg border bg-white p-5 shadow-sm">
        <div className="flex justify-between items-center">
          <div>
            <h1 className="text-xl font-bold text-slate-900">Documents</h1>
            <p className="text-sm text-slate-600">
              เอกสารทั้งหมด และสถานะล่าสุด
            </p>
          </div>

          <div className="flex gap-2">
            <button
              onClick={fetchComparisons}
              className="inline-flex items-center gap-2 rounded-md bg-blue-600 px-4 py-2 text-sm font-semibold text-white hover:bg-blue-700"
            >
              <ArrowPathIcon className={`h-4 w-4 ${loading ? "animate-spin" : ""}`} />
              Refresh
            </button>

            <button
              onClick={() => {}}
              className="inline-flex items-center gap-2 rounded-md border px-4 py-2 text-sm font-semibold text-slate-700 hover:bg-slate-50"
            >
              <DocumentArrowDownIcon className="h-4 w-4" />
              Export
            </button>
          </div>
        </div>

        {error && (
          <div className="mt-4 rounded-md bg-red-50 p-3 text-sm text-red-700">
            {error}
          </div>
        )}
      </div>

      {/* Filters */}
      <div className="rounded-lg border bg-white p-4 shadow-sm">
        <div className="flex gap-3 items-center">
          <div className="flex items-center gap-2 flex-1 rounded-md border px-3 py-2">
            <MagnifyingGlassIcon className="h-4 w-4 text-slate-400" />
            <input
              value={q}
              onChange={(e) => setQ(e.target.value)}
              placeholder="ค้นหาเอกสาร..."
              className="w-full text-sm outline-none"
            />
          </div>

          <select
            value={riskFilter}
            onChange={(e) => setRiskFilter(e.target.value as any)}
            className="rounded-md border px-3 py-2 text-sm"
          >
            <option value="ALL">All</option>
            <option value="HIGH">High</option>
            <option value="MEDIUM">Medium</option>
            <option value="LOW">Low</option>
          </select>
        </div>
      </div>

      {/* List */}
      <div className="rounded-lg border bg-white shadow-sm divide-y">
        {computed.filtered.map((r) => (
          <div
            key={r.doc}
            className="p-4 flex justify-between items-center hover:bg-slate-50"
          >
            <div className="min-w-0">
              <div className="flex items-center gap-2">
                <div className="font-semibold truncate">{r.doc}</div>
                <RiskPill r={r.lastRisk} />
              </div>
              <div className="text-xs text-slate-500 mt-1">
                {r.comparisons} comparisons • Updated {formatDate(r.lastUpdatedISO)}
              </div>
            </div>

            <Link
              href={`/compare/${r.lastComparisonId}`}
              className="rounded-md bg-blue-600 px-4 py-2 text-sm font-semibold text-white hover:bg-blue-700"
            >
              Open
            </Link>
          </div>
        ))}
      </div>
    </div>
  );
}
